
.tasks{
    list-style: none;
    gap: 10px;
    padding: 10px;
}
.listItem{
    list-style: none;
    display: grid;
    grid-template-columns: auto 1fr auto;
    padding: 10px;
    border-bottom: 2px solid #eee;
}
.listItemHidden{
    display: none;
}

.taskButton--done{
    background-color: hsl(120, 92%, 33%);
    color: white;
    width: 31px;
    height: 31px;
    border: none;
    text-decoration: none;
    transition: 1s;
}

.taskButton--done:hover{
    background-color:  hsl(120, 92%, 45%);

}

.taskButton--remove{
    background-color: hsl(0, 100%, 50%);
    color: white;
    height: 31px;
    width: 31px;
    border: none;
    text-decoration: none;
    transition: 1s;
}

.taskButton--remove:hover {
    background-color: hsl(0, 100%, 65%);
    
}

.taskButton--done{
    text-decoration: none;
}

.taskContent{
    text-align: start;
    padding: 10px;

 }
 
 .listItemDone{
    text-decoration: line-through;
}
